import { Component, OnInit } from '@angular/core';
import { ProcesosService } from 'src/app/services/procesos.service';

@Component({
  selector: 'app-procesos',
  templateUrl: './procesos.component.html',
  styleUrls: ['./procesos.component.css']
})
export class ProcesosComponent implements OnInit {

  constructor(public servicio:ProcesosService) { }
data:any=[];
pids:any=[];
nombres:any=[];
usuarios:any=[];
estado:any=[];
pidsh:any=[];
nombresh:any=[];
usuariosh:any=[];
estadoh:any=[];
datares:any=[];
verhijos =false;
  ngOnInit(): void {
    this.obtenerResumen();
    this.obtenerDatos();
  }
  obtenerResumen(){

    this.servicio.GetValuesResumen().subscribe(
      res=>{
        this.datares=res;
        
      },
      err=>{
        console.log(err);
      }
    );
   
  }
  obtenerDatos(){
    this.servicio.GetValues().subscribe(
      res=>{
        this.data=res;
        
      },
      err=>{
        console.log(err);
      }
    );
  }
  kill(pid:any){
    console.log("se eliminara "+pid)
    this.servicio.kill(pid).subscribe(
     res=>{
    
     },err=>{
      console.log(err);
    }
    );
    this.obtenerDatos();
  }
 
}
